import React, {useState} from 'react';
import './App.css'
import {useNuiEvent} from "../hooks/useNuiEvent";
import {debugData} from "../utils/debugData";
import {fetchNui} from "../utils/fetchNui";
import { useEffect } from 'react';

// This will set the NUI to visible if we are
// developing in browser
debugData([
  {
    action: 'setVisible',
    data: true,
  }
])


const App: React.FC = () => {
  
  const [hidden, setHidden] = useState(true)
  const [isVisible, setIsVisible] = useState(false)
  const [width, setWidth] = useState(0)

  const [text, setText] = useState("HOLD E")
  const [desc, setDesc] = useState("to respawn or wait for EMS")

  const [showTime, setShowTime] = useState(true)
  const [time, setTime] = useState(0)

  const [status, setStatus] = useState("normal")
  const [blackbars, setBlackbars] = useState(false)
  const [preventCancel, setPreventCancel] = useState(false)

  useNuiEvent<boolean>('setVisible', (data) => {
    setIsVisible(data)
  })

  useNuiEvent<string>('updateText', (data: string) => {
    setText(data)
  })

  useNuiEvent<string>('updateDesc', (data: string) => {
    setDesc(data)
  })

  useNuiEvent<number>('updateWidth', (data: number) => {
    setWidth(data)
  })

  useNuiEvent<number>('updateTime', (data: number) => {
    setTime(data)
  })

  useNuiEvent<string>('updateStatus', (data: string) => {
    setStatus(data)
  })

  useNuiEvent<boolean>('updateBlackbars', (data: boolean) => {
    setBlackbars(data)
  })

  useNuiEvent<boolean>('preventCancel', (data: boolean) => {
    setPreventCancel(data)
  })

  useNuiEvent<boolean>('showTime', (data: boolean) => {
    console.log("Showing time!", data)
    setShowTime(data)
  })

  useEffect(() => {
    setTimeout(() => {
      setHidden(false)
    }, 100);
  }, [hidden])

  return (
    <div className={`nui-wrapper ${isVisible} blackbars-${blackbars}`} style={{ visibility: hidden ? 'hidden' : 'visible' }}  >
      <div className="progress-bar-text"> {text} </div>
      <div className="progress-bar-desc"> { showTime ? time : "" } {desc} </div>
      <div className={`progress-bar ${status}`} >
        <div className={`progress-inside-shadow ${status}`} style={{ width: "100%" }}></div>
        <div className={`progress-inside ${status}`} style={{ width: width + "%" }}></div>
      </div>

     

      <div className="progress-bar-cancel"  style={{display: preventCancel ? 'none' : '' }} > 

        <svg className="rmb" style={{ paddingRight: '8px' }} width="24" height="22" viewBox="0 0 24 22" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path d="M18.0042 9.00417L16.0792 11L18.0042 12.9583C18.4292 13.35 18.4292 13.9833 18.0042 14.3375C17.65 14.7625 17.0167 14.7625 16.625 14.3375L14.6667 12.4125L12.6708 14.3375C12.3167 14.7625 11.6833 14.7625 11.2917 14.3375C10.9042 13.9833 10.9042 13.35 11.2917 12.9583L13.2542 11L11.2917 9.00417C10.9042 8.65 10.9042 8.01667 11.2917 7.625C11.6833 7.2375 12.3167 7.2375 12.6708 7.625L14.6667 9.5875L16.625 7.625C17.0167 7.2375 17.65 7.2375 18.0042 7.625C18.4292 8.01667 18.4292 8.65 18.0042 9.00417ZM21.3333 3C22.8042 3 24 4.19375 24 5.66667V16.3333C24 17.8042 22.8042 19 21.3333 19H8.55417C7.84583 19 7.16667 18.7208 6.66667 18.2208L0.3905 11.9417C0.140458 11.6917 0 11.3542 0 11C0 10.6458 0.1405 10.3083 0.3905 10.0583L6.66667 3.78125C7.16667 3.28083 7.84583 3 8.55417 3H21.3333ZM22 5.66667C22 5.3 21.7 5 21.3333 5H8.55417C8.375 5 8.20417 5.07083 8.07917 5.19583L2.27625 11L8.07917 16.8042C8.20417 16.9292 8.375 17 8.55417 17H21.3333C21.7 17 22 16.7 22 16.3333V5.66667Z" fill="white"/>
        </svg>

        <span className="tocancel">to cancel</span>
      </div>
    </div>
  );
}

export default App;
